@import url(https://fonts.googleapis.com/css2?family=Carter+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
/*  font-family: 'Carter One', cursive; */


body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.nav a {
    text-decoration: none;
}

.App__Title {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 150px;
    font-family: 'Carter One', cursive;
    font-size: 4rem;
    color: black;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

.App__Title:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.App__Title:active {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.App__Title--Decor {
    -webkit-text-stroke: 3px black;
	-webkit-text-fill-color: white;
}

.App__SearchBar {
    position: relative;
    display: grid;
    grid-template-columns: auto 150px;
    grid-template-rows: 1fr;
    margin: 50px auto;
}

.App__SearchBar button {
    width: 100%;
    padding: 25px;
    border: none;
    font-size: 3rem;
    background: none;
    cursor: pointer;
    transition: 0.2s ease-out transform;
}

.App__SearchBar button:hover { -webkit-transform: scale(1.2); transform: scale(1.2)}

.App__SearchBar input {
    padding: 25px;
    border: none;
    background-color: rgb(250, 250, 250);
    font-size: 2rem;
}

.App__SearchBar input, .App__SearchBar button, .App__SearchBar__Hints { font-family: 'Carter One', cursive;}

.App__SearchBar__Hints {
    position: absolute;
    top: 100%;
    width: 100%;
    background: white;
    color: grey;
}

.App__SearchBar__Hints__Elem {
    padding: 25px;
    font-size: 2rem;
    border-bottom: 3px dashed grey;
}

@media only screen and (max-width: 750px) {
    .App__Title {
        margin: 75px;
    }
}

@media only screen and (max-width: 500px) {
    .App__Title {
        margin: 20px;
        font-size: 2rem;
    }

    .App__Title--Decor {
        -webkit-text-stroke: 1px black;
        -webkit-text-fill-color: white;
    }
    
    .App__SearchBar {
        grid-template-columns: 1fr auto;
        grid-template-rows: 1fr;
        margin: 25px auto;
    }

    .App__SearchBar input, .App__SearchBar button {
        font-size: 1rem;
    }
}

.HeroSimplified {
    display: grid;
    grid-template-rows: auto 1fr auto;
    grid-template-columns: 1fr;
    font-family: 'Carter One', cursive;
    border: 3px dashed rgb(207,207,207);
    border-radius: 15px;
    transition: 0.2s ease-in border-color;
}

.HeroSimplfied__Name { 
    font-size: 3rem;
    text-align: center;
}

.HeroSimplified__Image {
    justify-self: center;
    border-radius: 15px;
}

.HeroSimplified__PowerstatsBox {
    display: flex;
    padding: 50px 15px 15px 15px;
    justify-content: space-evenly;
    font-size: 2rem;
}

.HeroSimplified:hover {
    cursor: pointer;
    border-color: black;
}

.HeroSimplified a {
    margin: 0 auto;
}

@media only screen and (max-width: 500px) {
    .HeroSimplfied__Name {
        font-size: 1rem;
    }

    .HeroSimplified__PowerstatsBox {
        font-size: 1rem;
    }

    .HeroSimplified__Image {
        width: 200px;
    }
}
.App__Grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
    grid-gap: 50px;
    gap: 50px;
    width: 100%;
  }

  @media only screen and (max-width: 500px) {
    .App__Grid {
      grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    }

    img {
      width: 20px;
    }
  }
.HeroDetailed {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.HeroDetailed img {
    max-height: 640px;
    max-width: 360px;
}

.HeroDetailed__Name {
    font-family: 'Carter One', cursive;
    font-size: 3rem;
    grid-area: name;
}


.HeroDetailed__Appearance__One, .HeroDetailed__Appearance__Two, .HeroDetailed__Biography {
    padding: 30px;
    border: 1px solid rgb(230, 230, 230);
}

.HeroDetailed__Appearance__One {
    grid-area: appOne;
}

.HeroDetailed__Appearance__Two {
    grid-area: appTwo;
}

.HeroDetailed__Biography {
    grid-area: bio;
}

.HeroDetailed--CategoryName {
    font-weight: 700;
    color: grey;
}

.HeroDetailed__InfoBox {
    display: grid;
    grid-template-columns: repeat(2, minmax(auto, 1fr));
    grid-template-rows: auto auto auto;
    grid-gap: 30px;
    grid-template-areas:
        "name name"
        "appOne appTwo" 
        "bio bio";
}


@media only screen and (max-width: 550px) {
    .HeroDetailed__Name {
        font-size: 1rem;
    }
}

@media only screen and (max-width: 970px) {
    .HeroDetailed {
        flex-direction: column;
        align-items: center;
    }
}
.SearchList__Info {
    color: grey;
}
footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 150px;
    width: 100%;
}

@media only screen and (max-width: 500px) {
    footer {
        flex-direction: column;
        justify-content: center;
    }
}
.Container {
  margin: 0 auto;
  max-width: 1180px;
}
